
h1 {
    text-align: center;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 700;
  }
  
  .elo {
    width: 100%;
    min-width: 300px;
    max-width: 300px;
    background: rgba(252, 181, 181, 0.7);
    padding: 2rem;
    border-radius: 15px;
  }
  
  #result,
  #instruction {
    margin: 1rem;
  }
  
  .d-flex {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .img {
    display: flex;
    justify-content: center;
  }
  
  #elo {
    margin: 1rem;
    max-width: 100px;
    width: 100%;
    height: auto;
  }
  

  input {
    border: none;
    border-radius: 5px;
    margin-bottom: 0.5rem;
  }
  
  button {
    width: 100%;
    border: none;
    padding: 16px;
    border-radius: 3px;
    line-height: 1.25;
    background: #782A6D;
    text-decoration: none;
    color: white;
    font-size: 12px;
    letter-spacing: .08em;
    text-transform: uppercase;
    position: relative;
    transition: background-color .6s ease;
    overflow: hidden;
  
    &:after {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      top: 50%;
      left: 50%;
      top: var(--mouse-y);
      left: var(--mouse-x);
      transform-style: flat;
      transform: translate3d(-50%, -50%, 0);
      background: rgba(white, .1);
      border-radius: 100%;
      transition: width .3s ease, height .3s ease;
    }
  
    &:focus,
    &:hover {
      background: darken(#FC6E51, 7%);
    }
  
    &:active {
      &:after {
        width: 300px;
        height: 300px;
      }
    }
  }
  
  @media (max-width: 768px) {
    .elo {
      width: 90%;
    }
  }